.maxim {
  display: flex;
  padding: 2rem 3rem;
  background-color: white;
  position: relative;
  flex-direction: column;
}
.maxim-label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 70px;
  color: var(--label);
  flex: 1;
}
.maxim-swiper-wrapper {
  flex: 1;
}
.maxim p {
  display: flex;
  align-items: center;
  justify-content: center;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.maxim-wrapper {
  display: flex;
  flex-direction: column;
}

.maxim .swiper-pagination-bullet-active {
  background: var(--label);
}

.maxim .swiper {
  height: 10rem;
}
